import * as actionTypes from '../actions/actionTypes';

const initialState = {
    service: "",
    person: "",
    speciality: "",
    doctor: "",
    timePart: "",
    time: "",
    note: ""
}

const AppReducer = (state = initialState, action)=>{
    switch(action.type){
        case actionTypes.SET_SERVICE: 
            return {
                ...state, 
                service: action.title
            };
        case actionTypes.SET_PERSON:
            return {
                ...state,
                person: action.title
            };
        case actionTypes.SET_SPECIALITY:
            return {
                ...state,
                speciality: action.title
            };
        case actionTypes.SET_DOCTOR:
            return {
                ...state, 
                doctor: action.title
            }
        case actionTypes.SET_TIME: 
            return {
                ...state,
                time: action.time
            };
        case actionTypes.SET_TIME_PART:
            return {
                ...state, 
                timePart: action.time
            }
        case actionTypes.SET_NOTE:
            return {
                ...state, 
                note: action.note
            }
        default:
            return {
            ...state
        }
    }
}


export default AppReducer;