import React from 'react';
import classes from './Bars.module.css';
import Bar from '../UI/Bar/Bar';


const Bars = ({step})=>{
    return (
        <div className={classes.BarsContainer}>
            <Bar active={step >= 1 ? true: false}/>
            <Bar active={step >= 2 ? true: false}/>
            <Bar active={step >= 3 ? true: false}/>
            <Bar active={step >= 4 ? true: false}/>
            <Bar active={step >= 5 ? true: false}/>
        </div>
    )
}


export default Bars;