import React from "react";
import classes from "./List.module.css";
import Avatar from "../UI/Avatar/Avatar";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";

const List = ({ title, url, right, vertical, subTitle, click, current }) => {
  const style = [classes.container, classes.vertical];
    let active = {}
  if(current === title){
    active = {backgroundColor: 'var(--blue-shade)', color: 'white'}
  }

  return (
    <div
      className={vertical ? style.join(" ") : style[0]}
      onClick={click.bind(this, title)}
      style={active}
    >
      {url && <Avatar url={url} />}
      <p className={classes.avatarName}>{title}</p>
      {subTitle && <p className={classes.subTitle}>{subTitle}</p>}
      {right && <ChevronRightIcon />}
    </div>
  );
};

export default List;
