import React from "react";


const spanStyle = {
  fontSize: "16px",
};

const SubHeading = ({ title, subTitle, color = "black", size = '20px' }) => {
  return (
    <h1 style={{fontSize: size, color}}>
      {title} 
      {subTitle && <span style={spanStyle}> {subTitle}</span>}
    </h1>
  );
};


export default SubHeading;
