import React from "react";
import classes from "./Details.module.css";
import Avatar from "../UI/Avatar/Avatar";
import Heading from "../UI/Heading/Heading";
import Button from "../UI/Button/Button";

const Details = ({ url, name, speciality, exp, address, buttons, onBook}) => {
  return (
    <div className={classes.container}>
      <div className={classes.firstContainer}>
        <Avatar url={url} />
        <div className={classes.content}>
          <Heading title={name} />
          <p className={classes.subHeading}>
            {speciality}, {exp}yrs exp.
          </p>
          <p className={classes.address}>{address}</p>
        </div>
      </div>
      {buttons && <div className={classes.btns}>
        <Button title="View Profile" borderColor="#5E58FF" color="#5E58FF" />
        <Button
          title="Book Appointment"
          borderColor="#5E58FF"
          background="#5E58FF"
          color="white"
          click={onBook.bind(this, name)}
        />
      </div>}
    </div>
  );
};

export default Details;
