import React from "react";
import classes from "./Home.module.css";
import Image from "../../component/UI/Image/Image";
import SubHeading from "../../component/UI/SubHeading/SubHeading";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import NotificationsNoneIcon from "@material-ui/icons/NotificationsNone";
import Heading from "../../component/UI/Heading/Heading";
import SearchBox from "../../component/UI/SearchBox/SearchBox";
import List from "../../component/List/List";
import { connect } from 'react-redux';
import * as actions from '../../store/actions/appActions';

const Home = (props) => {

    const selectListHandler = (title)=>{
        props.onSelect(title);
        props.history.push('/first');
    }

  return (
    <div className={classes.container}>
      <div className={classes.Header}>
        <div className={classes.location}>
          <div>
            <Image url="./assests/medocal icons/location.png" />
            <SubHeading
              title={"Your location"}
              color="rgb(200, 200, 200)"
              size={14}
            />
          </div>
          <p className={classes.address}>
            123 fictional Steet... <ExpandMoreIcon />
          </p>
        </div>
        <div className={classes.notification}>
          {/* <div className={}></div>  BADGE DIV */}
          <NotificationsNoneIcon style={{ fontSize: "35px" }} />
        </div>
      </div>
      <Heading title="What are you looking for?" />
      <SearchBox />
      <div className={classes.listContainer}>
        <div className={classes.list}>
          <List
            title="General Medicine"
            url="./assests/booking.png"
            vertical
            subTitle="Book and appointment"
            click={selectListHandler}
            current={props.service}
          />
          <List
            title="Heart"
            url="./assests/booking.png"
            subTitle="Book and appointment"
            vertical
            click={selectListHandler}
            current={props.service}
          />

          <List
            title="General Surgery"
            url="./assests/booking.png"
            vertical
            subTitle="Book and appointment"
            click={selectListHandler}
            current={props.service}
          />
          <List
            title="Eye Specialist"
            url="./assests/booking.png"
            vertical
            subTitle="Book and appointment"
            click={selectListHandler}
            current={props.service}
          />
        </div>
        <div className={classes.list}>
          <List
            title="Women's Health"
            url="./assests/booking.png"
            vertical
            subTitle="Book and appointment"
            click={selectListHandler}
            current={props.service}
          />
          <List
            title="Ayurveda"
            url="./assests/booking.png"
            vertical
            subTitle="Book and appointment"
            click={selectListHandler}
            current={props.service}
          />
          <List
            title="Sex Specialist"
            url="./assests/booking.png"
            vertical
            subTitle="Book and appointment"
            click={selectListHandler}
            current={props.service}
          />
          <List
            title="Dental Care"
            url="./assests/booking.png"
            vertical
            subTitle="Book and appointment"
            click={selectListHandler}
            current={props.service}
          />
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state)=>{
    return {
        service: state?.service
    }
}

const mapDispatchToProps = (dispatch)=>{
    return {
        onSelect: (title)=> dispatch(actions.setService(title))
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Home);
