import React from 'react';
import './app.css';
import { HashRouter as Router, Route } from 'react-router-dom';
import {connect} from 'react-redux';

import Tab from './component/Bottom-Tab/Tab';
import Booked from './screens/bookedScreen/booked';
import Home from './screens/HomeScreen/Home';
import First from './screens/firstScreen/first';
import Second from './screens/SecondScreen/Second';
import Third from './screens/ThirdScreen/Third';
import Fourth from './screens/FourthScreen/fourth';
import Fifth from './screens/fifthScreen/FIfth';


const App = (props)=>{
    return (
        <div>
            <Router>
            <Route path="/" exact component={Home} />
            <Route path="/first"  component={First} />
            <Route path="/second" component={Second} />
            <Route path="/third" component={Third} />
            <Route path="/fourth" component={Fourth} />
            <Route path="/fifth" component={Fifth} />
            <Route path="/booked" component={Booked} />
            </Router>
            <Tab />
        </div>
    )
}


export default connect()(App);