import * as actionTypes from './actionTypes';


export const setService = (title)=>{ 
    return {
        type: actionTypes.SET_SERVICE,
        title
    }
}

export const setPerson = (title)=>{
    return {
        type: actionTypes.SET_PERSON,
        title
    }
}

export const setSpeciality = (title)=>{
    return {
        type: actionTypes.SET_SPECIALITY,
        title
    }
}

export const setDoctor = (title)=>{
    return {
        type: actionTypes.SET_DOCTOR,
        title
    }
}

export const setTime = (time)=>{
    return {
        type: actionTypes.SET_TIME,
        time
    }
}

export const setTimePart = (time)=>{
    return {
        type: actionTypes.SET_TIME_PART,
        time
    }
}


export const setNote = (note)=>{
    return {
        type: actionTypes.SET_NOTE,
        note
    }
}