import React from 'react';
import classes from './booked.module.css';
import Heading from '../../component/UI/Heading/Heading'; 
import Image from '../../component/UI/Image/Image'; 
import Button from '../../component/UI/Button/Button';

import {connect} from 'react-redux';

const booked = (props)=>{
    console.log(props)

    return (
        <div className={classes.container}>
            <div className={classes.contentSection}>
               <Heading title={"Appointment booked with" + props.doctor}/> 
               <Heading title={"Mon, 25Feb - "+ props.time} color="var(--blue-shade)"/> 
               <Button title="Add to Calender" url="./assests/medocal icons/calenar.png"/>
            </div>
            <div className={classes.visualSection}>
                <Image url="./assests/booking.png" />
            </div>
        </div>
    );
}

const mapStateToProps = (state)=>{
    return {
        ...state
    }
}


export default connect(mapStateToProps)(booked);