import React from "react";
import classes from "./first.module.css";
import Heading from "../../component/UI/Heading/Heading";
import List from "../../component/List/List";
import Navigation from "../../component/Navigation/Navigation";
import Bars from "../../component/Bars/Bars";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";

import * as actions from '../../store/actions/appActions';
import {connect} from 'react-redux';

const first = (props) => {

  const listSelectHandler = (title)=>{
    props.onSelect(title);
  }

  return (
    <div className={classes.container}>
      <Bars step={1} />
      <Heading title="Appointment for?" />
      <div className={classes.listContainer}>
        <List title="Myself" url="./assests/booking.png" click={listSelectHandler} current={props.person}/>
        <List title="Wife" url="./assests/booking.png" click={listSelectHandler} current={props.person}/>
        <List title="Parent" url="./assests/booking.png" click={listSelectHandler} current={props.person}/>
        <List title="Child" url="./assests/booking.png" click={listSelectHandler} current={props.person}/>
      </div>
      <Navigation
        left={<ArrowBackIcon style={{ fontSize: "30px" }} />}
        right={<ArrowForwardIcon style={{ fontSize: "30px" }} />}
        leftClick={()=> props.history.push('/')}
        rightClick={()=> props.history.push('/second')}
      />
    </div>
  );
};

const mapStateToProps = state => {
  return {
    person: state?.person
  }
}

const mapDispatchToProps = (dispatch)=>{
  return {
    onSelect: (title)=> dispatch(actions.setPerson(title))
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(first);
